
.contact {
  padding: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#message {
  text-align: center;
}
.contact-container {
  width: 75%;
  margin: 0 auto;
}
.contact-form label {
  display: block;
  margin-bottom: 5px;
}
.user-input {
  border-radius: 2px;
  border: 1px solid grey;
  margin-bottom: 10px;
  height: 30px;
  width: 750px;
  padding: 5px;
}
textarea.user-input {
  height: 50px;
}



